                                    </li>
                                    <li>
                                        противоречащи на нравствените норми предмети;
                                    </li>
                                    <li>
                                        религиозни материали на забранени или нерегистрирани в страната секти и организации;
                                    </li>
                                    <li>
                                        движими паметници на културата, за които няма издадено разрешение или сертификат;
                                    </li>
                                </ol>
                            </li>
                            <li>
                            В пощенските пратки не се допуска поставянето на монети, банкноти, парични знаци, чекове за пътуване, предмети, представляващи ценност за подателя, платина, злато, сребро, обработени или необработени скъпоценни камъни и други ценни предмети.
                            </li>
                            <li>
                            Не се приемат пощенски пратки, които съдържат животни или нетрайни биологични вещества, включително диагностични образци, ако опаковката им не отговаря на изискванията, предвидени в нормативната уредба на Република България, и приложимите международни актове.
                            </li>
                            <li>
