                            </li>
                            <li>
                            Във връзка с проверката на изпълнението на условията и забраните по този раздел, Куриерът изисква съгласието на подателя да извърши проверка чрез отваряне на пратката и преглед на съдържанието. В случай на отказ пратката не се приема.
                            </li>
                            <li>
                            В случай, че куриерът прецени, че пратката не отговаря на условията и забраните, посочени в този раздел, той има право да задържи пратката и да спре доставянето и да изиска от подателя или получателя писмено съгласие за извършване на проверка на съдържанието чрез отварянето й. При отказ или липса на отговор Куриерът уведомява компетентните власти за получаване на разпореждане за следващи действия.
                            </li>
                        </ol>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='text-md-h5 text-sm-h6 font-weight-black'>
                        <strong>Раздел X</strong>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='text-md-h6 text-sm-h6 font-weight-bold'>
                        <u>Рекламации и обезщетения</u>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='font-weight-bold'>
                        <ol start='56'>
                            <li>
                            Потребителите имат право на рекламации при неспазване сроковете за доставка на пратките, в случаи на повредени, загубени, ограбени или унищожени пратки и при неизпълнение от страна на Куриера на договорените задължения.
                            </li>
                            <li>
                            Рекламация към Куриера може да бъде подадена в срок от 6 месеца, считано от датата на приемане на пратката.
                            </li>
                            <li>
                            При получаване на пратката Потребителят трябва да провери съдържанието й в момента на получаването и в присъствието на служител на Куриера.
                            </li>
                            <li>
                            В случай, че при получаването се установи увреждане на съдържанието на пратката, както и при доставяне на пратки с повредена опаковка, за които има основания да се счита, че е налице посегателство върху съдържанието на доставената пратката спрямо това при приемането й от Куриера, се съставя констативен протокол в два еднообразни екземпляра по образец, който се подписва от служителя и от получателя на пратката. В протокола се вписват данни относно датата и часа на доставката, констатираното увреждане и евентуалните причини за него, ако има данни за това. Ако получателят откаже да подпише протокола, служител на Куриерът удостоверява отказа. Получателят може да направи рекламация в срок от 6 месеца от датата на съставяне на констативния протокол, ако счита, че Куриерът има вина за констатираните увреждания по пратката.
                            </li>
                            <li>
                            Като задължително условие за редовно предявяване на Рекламацията се счита заплащането на всички дължими на Куриера суми за предоставени услуги без отлагане или прихващане.
                            </li>
                            <li>
                            Проучването на Рекламацията и писменото уведомяване на Потребителя за резултата от него се извършва от Куриера в срок до 1 месец от предяваването й. В случай на подадени непълни данни в рекламацията, срокът се удължава с времето, необходимо за уточняване на липсващата информация.
                            </li>
                            <li>
                            По предявена от Потребител основателна рекламация за повредена, унищожена или загубена пратка Куриерът дължи обезщетение в размер, ограничен до максимум 50 лева.
                            </li>
                            <li>
                            Операторът не носи отговорност, когато забавянето или невъзможността за изпълнение на задълженията се дължи на непредвидени обстоятелства, възникнали при осъществяване на транспортирането. В този случай срокът за доставка се удължава автоматично с броя дни на забавянето, поради причина, която не зависи от Куриера.
                            </li>
                            <li>
                            Срокът за изплащане на обезщетения по предявени основателни рекламации е 15 работни дни след датата на изпращане на отговора, с който Куриерът приема рекламацията и посочва размера на дължимото обезщетение.
                            </li>
                        </ol>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='text-md-h5 text-sm-h6 font-weight-black'>
                        <strong>Раздел XI</strong>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='text-md-h6 text-sm-h6 font-weight-bold'>
                        <u>Ред за решаване на спорове</u>
                    </v-card-text>

                    <v-card-text align='left' justify='center' class='font-weight-bold'>
                        <ol start='65'>
                            <li>
                            В случай, че между Потребителя и Куриера възникнат спорове във връзка с която и да е дейност от страна на страните, свързана с предоставянето на услугите, разрешаването им се постига чрез добросъвестно сътрудничество, изразяващо се в преговори и допълнителни споразумения. Ако в резултат на това не се стигне до взаимноприемливо разрешаване на споровете, всяка от страните може да поиска съдействие от Комисията за регулиране на съобщенията, други компетентни органи или да отнесе спора за решаване пред компетентния български съд.
                            </li>
                        </ol>
                    </v-card-text>

